// Baserow API Client

import {
    BaserowListResponse,
    BaserowBook,
    BaserowAuthor,
    BaserowUser,
    BaserowBorrowedBook,
    mapBaserowBookToBook,
    mapBaserowBorrowedBook,
    Book,
    BorrowedBook,
} from "./types"

const BASEROW_API_URL = process.env.NEXT_PUBLIC_BASEROW_API_URL || "https://api.baserow.io"
const BASEROW_API_TOKEN = process.env.NEXT_PUBLIC_BASEROW_API_TOKEN || ""

// Table IDs
const TABLE_BOOKS = process.env.NEXT_PUBLIC_BASEROW_TABLE_BOOKS || "772570"
const TABLE_AUTHORS = process.env.NEXT_PUBLIC_BASEROW_TABLE_AUTHORS || "772588"
const TABLE_PUBLISHERS = process.env.NEXT_PUBLIC_BASEROW_TABLE_PUBLISHERS || "772593"
const TABLE_USERS = process.env.NEXT_PUBLIC_BASEROW_TABLE_USERS || "772601"
const TABLE_BORROW_BOOKS = process.env.NEXT_PUBLIC_BASEROW_TABLE_BORROW_BOOKS || "772607"

/**
 * Base fetch function for Baserow API
 */
async function baserowFetch<T>(
    endpoint: string,
    options: RequestInit = {}
): Promise<T> {
    const url = `${BASEROW_API_URL}${endpoint}`

    const response = await fetch(url, {
        ...options,
        headers: {
            "Authorization": `Token ${BASEROW_API_TOKEN}`,
            "Content-Type": "application/json",
            ...options.headers,
        },
    })

    if (!response.ok) {
        const error = await response.text()
        throw new Error(`Baserow API error: ${response.status} - ${error}`)
    }

    return response.json()
}

/**
 * Get all books with author data
 */
export async function getBooks(): Promise<Book[]> {
    const response = await baserowFetch<BaserowListResponse<BaserowBook>>(
        `/api/database/rows/table/${TABLE_BOOKS}/?user_field_names=true&size=200`
    )
    return response.results.map(mapBaserowBookToBook)
}

/**
 * Get a single book by ID
 */
export async function getBook(id: string): Promise<Book | null> {
    try {
        const book = await baserowFetch<BaserowBook>(
            `/api/database/rows/table/${TABLE_BOOKS}/${id}/?user_field_names=true`
        )
        return mapBaserowBookToBook(book)
    } catch {
        return null
    }
}

/**
 * Get all authors
 */
export async function getAuthors(): Promise<BaserowAuthor[]> {
    const response = await baserowFetch<BaserowListResponse<BaserowAuthor>>(
        `/api/database/rows/table/${TABLE_AUTHORS}/?user_field_names=true`
    )
    return response.results
}

/**
 * Get user by email (for login)
 */
export async function getUserByEmail(email: string): Promise<BaserowUser | null> {
    try {
        const response = await baserowFetch<BaserowListResponse<BaserowUser>>(
            `/api/database/rows/table/${TABLE_USERS}/?user_field_names=true&filter__email__equal=${encodeURIComponent(email)}`
        )
        return response.results[0] || null
    } catch {
        return null
    }
}

/**
 * Get user by ID
 */
export async function getUserById(id: string): Promise<BaserowUser | null> {
    try {
        const user = await baserowFetch<BaserowUser>(
            `/api/database/rows/table/${TABLE_USERS}/${id}/?user_field_names=true`
        )
        return user
    } catch {
        return null
    }
}

/**
 * Get borrowed books for a user
 */
export async function getBorrowedBooks(userId: string): Promise<BorrowedBook[]> {
    try {
        const response = await baserowFetch<BaserowListResponse<BaserowBorrowedBook>>(
            `/api/database/rows/table/${TABLE_BORROW_BOOKS}/?user_field_names=true&filter__User__link_row_has=${userId}`
        )
        return response.results.map(mapBaserowBorrowedBook)
    } catch {
        return []
    }
}

/**
 * Create a borrow record
 */
export async function createBorrowRecord(data: {
    bookId: string
    userId: string
    dueDate: string
}): Promise<BaserowBorrowedBook | null> {
    try {
        const response = await baserowFetch<BaserowBorrowedBook>(
            `/api/database/rows/table/${TABLE_BORROW_BOOKS}/?user_field_names=true`,
            {
                method: "POST",
                body: JSON.stringify({
                    Book: [data.bookId],
                    User: [data.userId],
                    Borrowed_At: new Date().toISOString(),
                    Due_Date: data.dueDate,
                    Status: "borrowed",
                }),
            }
        )
        return response
    } catch {
        return null
    }
}

/**
 * Update a borrow record (e.g., mark as returned)
 */
export async function updateBorrowRecord(
    id: string,
    data: Partial<{
        Returned_At: string
        Status: "borrowed" | "returned" | "overdue"
    }>
): Promise<BaserowBorrowedBook | null> {
    try {
        const response = await baserowFetch<BaserowBorrowedBook>(
            `/api/database/rows/table/${TABLE_BORROW_BOOKS}/${id}/?user_field_names=true`,
            {
                method: "PATCH",
                body: JSON.stringify(data),
            }
        )
        return response
    } catch {
        return null
    }
}

/**
 * Update book available copies
 */
export async function updateBookCopies(
    bookId: string,
    availableCopies: number
): Promise<BaserowBook | null> {
    try {
        const response = await baserowFetch<BaserowBook>(
            `/api/database/rows/table/${TABLE_BOOKS}/${bookId}/?user_field_names=true`,
            {
                method: "PATCH",
                body: JSON.stringify({
                    Available_Copies: availableCopies,
                }),
            }
        )
        return response
    } catch {
        return null
    }
}

/**
 * Create a new user (for sign-up)
 */
export async function createUser(data: {
    email: string
    password: string
    fullName?: string
}): Promise<BaserowUser | null> {
    try {
        const response = await baserowFetch<BaserowUser>(
            `/api/database/rows/table/${TABLE_USERS}/?user_field_names=true`,
            {
                method: "POST",
                body: JSON.stringify({
                    email: data.email,
                    password: data.password, // Should be hashed before sending
                    username: data.fullName || null,
                    is_admin: false,
                }),
            }
        )
        return response
    } catch {
        return null
    }
}

