// Baserow API Types

/**
 * Generic Baserow list response wrapper
 */
export interface BaserowListResponse<T> {
    count: number
    next: string | null
    previous: string | null
    results: T[]
}

/**
 * Author from Baserow (table 772588)
 */
export interface BaserowAuthor {
    id: string
    value: string
}

/**
 * Publisher from Baserow (table 772593)
 */
export interface BaserowPublisher {
    id: string
    value: string
}

/**
 * Book from Baserow (table 772570)
 * Field names match actual Baserow table columns
 */
export interface BaserowBook {
    id: string
    order: string
    book_id?: string | null
    author_name?: string | BaserowAuthor[] | null
    title: string
    place_of_publication?: string | null
    publisher_name?: string | BaserowPublisher[] | null
    year_of_publication?: number | null
    edition?: string | null  // Note: typo in original field name
    price?: number | null
    class_number?: string | null
    source?: string | null
    notes?: string | null
    "lost/damage"?: string | null
    note?: string | null
}

/**
 * User from Baserow (table 772601)
 */
export interface BaserowUser {
    id: string
    order: string
    user_id?: string | null
    username?: string | null
    email: string
    Email?: string
    password: string
    Password?: string // Handle capitalized field name
    is_admin: boolean
    Is_Admin?: boolean // Handle capitalized field name
}

/**
 * Borrowed Book from Baserow (table 772607)
 */
export interface BaserowBorrowedBook {
    id: string
    order: string
    Book?: BaserowBook[] | null  // Link to Books table
    User?: BaserowUser[] | null  // Link to Users table
    Borrowed_At: string
    Due_Date: string
    Returned_At?: string | null
    Status: "borrowed" | "returned" | "overdue"
}

/**
 * Session user (stored in cookie/localStorage)
 */
export interface SessionUser {
    id: string
    email: string
    fullName: string | null
    isAdmin: boolean
}

/**
 * Mapped types for frontend consumption
 */
export interface Author {
    id: string
    name: string
}

export interface Book {
    id: string
    book_id: string | null
    title: string
    author_name: string | null
    author_id?: string | null
    publisher_name: string | null
    place_of_publication: string | null
    published_year: number | null
    edition: string | null
    price: number | null
    class_number: string | null
    source: string | null
    notes: string | null
    created_at: string
    // Additional fields for UI components
    genre: string | null
    isbn: string | null
    description: string | null
    available_copies: number
    total_copies: number
    // For grid compatibility - map author_name to author object
    author: Author | null
}

export interface BorrowedBook {
    id: string
    book_id: string
    user_id: string
    borrowed_at: string
    due_date: string
    returned_at: string | null
    status: "borrowed" | "returned" | "overdue"
    book: Book | null
}

export interface UserProfile {
    id: string
    user_id: string | null
    username: string | null
    email: string | null
    is_admin: boolean
    created_at: string
    updated_at: string
}

/**
 * Mapper functions to convert Baserow types to frontend types
 */
export function mapBaserowAuthorToAuthor(author: BaserowAuthor): Author {
    return {
        id: String(author.id),
        name: author.value,
    }
}

export function mapBaserowBookToBook(book: BaserowBook): Book {
    const authorName = Array.isArray(book.author_name) && book.author_name.length > 0
        ? book.author_name[0].value
        : (typeof book.author_name === 'string' ? book.author_name : null);

    return {
        id: String(book.id),
        book_id: book.book_id ?? null,
        title: book.title,
        author_name: authorName ?? null,
        publisher_name: Array.isArray(book.publisher_name) && book.publisher_name.length > 0 ? book.publisher_name[0].value : null,
        place_of_publication: book.place_of_publication ?? null,
        published_year: book.year_of_publication ?? null,
        edition: book.edition ?? null,
        price: book.price ?? null,
        class_number: book.class_number ?? null,
        source: book.source ?? null,
        notes: book.notes ?? null,
        created_at: new Date().toISOString(),
        // Additional fields for UI components - using defaults since not in Baserow
        genre: null,
        isbn: null,
        description: book.notes ?? null, // Use notes as description fallback
        available_copies: 1, // Default to 1 copy available
        total_copies: 1, // Default to 1 total copy
        author: authorName ? {
            id: "",
            name: authorName,
        } : null,
    }
}

export function mapBaserowUserToProfile(user: BaserowUser): UserProfile {
    return {
        id: String(user.id),
        user_id: user.user_id ?? null,
        username: user.username ?? null,
        email: user.email,
        is_admin: user.is_admin,
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString(),
    }
}

export function mapBaserowBorrowedBook(borrowed: BaserowBorrowedBook): BorrowedBook {
    const book = borrowed.Book?.[0]
    const user = borrowed.User?.[0]
    return {
        id: String(borrowed.id),
        book_id: book ? String(book.id) : "",
        user_id: user ? String(user.id) : "",
        borrowed_at: borrowed.Borrowed_At,
        due_date: borrowed.Due_Date,
        returned_at: borrowed.Returned_At ?? null,
        status: borrowed.Status,
        book: book ? mapBaserowBookToBook(book) : null,
    }
}
